.name {
    text-align: center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    color: white;
}

#my-name {
    letter-spacing: 3px;
}

.header-wrapper {
    width: 100%;
    background: #16151C;
    z-index: 1;
    position: fixed;
}

.home-wrapper {
    position: relative;
}

.container-button {
    display: inline-block;
    padding-left: 10px;
}

h2 {
    padding-bottom: 10px;
}

.btn-home {
    color: white;
    border: 2px solid;
    transition: all 0.4s ease-in-out;
    cursor: pointer;
}

.profile-links {
    margin-top: 20px;
    font-size: 50px;
}

.profile-links a {
    color: gray;
    margin-right: 16px;
    margin-left: 16px;
}

.profile-links a:hover {
    transition: all 0.5s;
    color: #A6E22E;
}

#right-arrow {
    display: inline-block;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    color: white;
    font-size: 15px;
}

.btn-home #right-arrow {
    transition: all 0.3s ease;
    right: 15px;
}

.btn-home:hover {
    background-color: #282c34;
    color: white;
}

.btn-home:hover #right-arrow {
    transform: rotateZ(90deg);
    transform-origin: center;
}
.nav {
    height: 50px;
    font-size: large;
    font-weight: normal;
    text-transform: uppercase;
}

.nav .nav-item {
    display: inline;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 35px;
    margin-left: 35px;
    background: #16151C;
}

.nav-link {
    color: white;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    border-bottom: 3px solid transparent;
    font-family: 'EB Garamond', sans-serif;
}

.nav-link:hover {
    border-bottom: 3px solid;
    color: white;
}

.btn-resume {
    font-size: large;
    font-weight: normal;
    text-transform: uppercase;
}

.nav .nav-item .nav-link.icon {
    display: none;
}

@media screen and (max-width: 768px) {
    .nav .nav-item:not(:last-child) {
        display: none;
    }
    .nav .nav-item:first-child {
        display: block;
    }
}

@media screen and (max-width: 768px) {
    .nav {
        display: block;
    }
    .nav .nav-item:first-child{
        float: left;
        margin: auto;
    }
    .nav .nav-item .nav-link.icon {
        float: right;
        display: block;
    }
    .nav .nav-item .nav-link.icon:hover {
        border-bottom: none;
    }
}

@media screen and (max-width: 768px) {
    .nav.responsive {
        position: relative;
    }
    .nav.responsive .nav-item .nav-link.icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .nav.responsive .nav-item {
        float: none;
        display: block;
        text-align: left;
        margin: auto;
    }
}
