@import url(https://fonts.googleapis.com/css?family=Raleway);

.portfolio-container {
    text-align: center;
    margin-right: auto;
    margin-left: auto;
    width: 1160px;
    color: black;
}

#portfolio-container {
    color: black;
    width: auto;
    height: auto;
}

.container {
    padding-top: 50px;
}

.portfolio-text {
    font-size: 25px;
}

.image-container{
    /*padding: 1em 0;*/
    float: left;
    font-family: 'Raleway', sans-serif;
    padding-bottom: 10px;
    /*width: 50%;*/
}

.image-container .title{
    color: #1a1a1a;
    text-align: center;
    margin-bottom: 10px;
}

.content {
    position: relative;
    /*width: 90%;*/
    /*max-width: 400px;*/
    margin: auto;
    /*overflow: hidden;*/
}

.content .content-overlay {
    background: rgba(0,0,0,0.7);
    position: absolute;
    /*height: 99%;*/
    /*width: 100%;*/
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    -webkit-transition: all 0.4s ease-in-out 0s;
    -moz-transition: all 0.4s ease-in-out 0s;
    transition: all 0.4s ease-in-out 0s;
}

.content:hover .content-overlay{
    opacity: 1;
}

.content-image{
    /*width: 100%;*/
    width: 350px;
    height: 350px;
    max-width: 100%;
    /*height: 100%;*/
    object-fit: cover;
}

.content-details {
    position: absolute;
    text-align: center;
    padding-left: 1em;
    padding-right: 1em;
    width: 100%;
    top: 50%;
    left: 50%;
    opacity: 0;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all 0.3s ease-in-out 0s;
    -moz-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
}

.content:hover .content-details{
    top: 50%;
    left: 50%;
    opacity: 1;
}

.content-details h3{
    color: #fff;
    font-weight: 500;
    letter-spacing: 0.15em;
    margin-bottom: 0.5em;
    text-transform: uppercase;
}

.content-details p{
    color: #fff;
    font-size: 0.8em;
}

.fadeIn-bottom{
    top: 80%;
}

@media screen and (max-width: 768px) {
    .portfolio-container {
        text-align: center;
        width: 100%;
    }
}
